<template>
  <v-container>
    <v-card class="card-2" variant="outlined" style="padding: 30px 40px;" @click="redirectToPage(props.item.id)">
      <div :style="{ height: imageHeight, margin: imageMargin }" class="d-flex flex-column align-center">
        <img
          :src="require(`@/assets/item/${props.item.id}.png`)"
          style="height: 100%;"
          @load="checkImageHeight"
        />
      </div>
      <div class="mt-5 d-flex flex-column align-center">
        <v-text class="text-h6 font-weight-bold">{{ props.item.name }}</v-text>
        <div class="mt-2 d-flex justify-space-between" style="width: 250px;">
          <v-text class="mb-1">
            <span>{{ getJobName(props.item.job) }}</span>
          </v-text>
          <v-text class="mb-1 text-right">
            <span>레벨 {{ props.item.level }} 이상</span>
          </v-text>
        </div>
        <v-card class="mt-2">
          <v-container class="d-flex flex-column" width="300" height="170" style="overflow-y: auto;">
            <v-text v-if="props.item.durability !== '0' && props.item.durability !== undefined" class="mb-1 d-flex justify-space-between">
              <span class="font-weight-bold">내구성</span>
              <span class="text-right">{{ props.item.durability }}</span>
            </v-text>
            
            <v-text v-if="props.item.destructionS !== '0' && props.item.destructionS !== undefined" class="mb-1 d-flex justify-space-between">
              <v-row>
                <v-col class="font-weight-bold" cols="3">파괴력</v-col>
                <v-col cols="9">
                  <div class="text-right">S: {{ props.item.destructionS }}</div>
                  <div class="text-right">L: {{ props.item.destructionL }}</div>
                </v-col>
              </v-row>
            </v-text>
            
            <v-text v-if="props.item.armament !== '0' && props.item.armament !== undefined" class="mb-1 d-flex justify-space-between">
              <span class="font-weight-bold">무장</span>
              <span class="text-right">{{ props.item.armament }}</span>
            </v-text>
            
            <v-text v-if="props.item.hit !== '0' && props.item.hit !== undefined" class="mb-1 d-flex justify-space-between">
              <span class="font-weight-bold">Hit</span>
              <span class="text-right">{{ props.item.hit }}</span>
            </v-text>
            
            <v-text v-if="props.item.damage !== '0' && props.item.damage !== undefined" class="mb-1 d-flex justify-space-between">
              <span class="font-weight-bold">Dam</span>
              <span class="text-right">{{ props.item.damage }}</span>
            </v-text>
            
            <v-text v-if="props.item.healthIncrease !== '0' && props.item.healthIncrease !== undefined" class="mb-1 d-flex justify-space-between">
              <span class="font-weight-bold">체력치 상승</span>
              <span class="text-right">{{ props.item.healthIncrease }}</span>
            </v-text>
            
            <v-text v-if="props.item.manaIncrease !== '0' && props.item.manaIncrease !== undefined" class="mb-1 d-flex justify-space-between">
              <span class="font-weight-bold">마력치 상승</span>
              <span class="text-right">{{ props.item.manaIncrease }}</span>
            </v-text>
            
            <v-text v-if="props.item.strangthIncrease !== '0' && props.item.strangthIncrease !== undefined" class="mb-1 d-flex justify-space-between">
              <span class="font-weight-bold">힘 상승</span>
              <span class="text-right">{{ props.item.strangthIncrease }}</span>
            </v-text>
            
            <v-text v-if="props.item.intelligenceIncrease !== '0' && props.item.intelligenceIncrease !== undefined" class="mb-1 d-flex justify-space-between">
              <span class="font-weight-bold">지력 상승</span>
              <span class="text-right">{{ props.item.intelligenceIncrease }}</span>
            </v-text>
            
            <v-text v-if="props.item.agilityIncrease !== '0' && props.item.agilityIncrease !== undefined" class="mb-1 d-flex justify-space-between">
              <span class="font-weight-bold">민첩성 상승</span>
              <span class="text-right">{{ props.item.agilityIncrease }}</span>
            </v-text>
          </v-container>
        </v-card>
      </div>
    </v-card>
  </v-container>
</template>

<script setup>
import { ref } from 'vue';
import { defineProps } from 'vue';
import { useRouter } from 'vue-router';

// props 정의
const props = defineProps({
  item: {
    type: Object,
    required: true
  }
});

const imageHeight = ref("100px");
const imageMargin = ref("");

const checkImageHeight = (event) => {
  const img = event.target;
  if (img.naturalHeight < 20) {
    imageHeight.value = "40px";
    imageMargin.value = "40px";
  } else {
    imageHeight.value = "100px";
  }
}

const getJobName = (job) => {
  const jobNames = [
    '직업제한무', // 0
    '전사용',    // 1
    '도적용',    // 2
    '주술사용',  // 3
    '도사용'     // 4
  ];
  return jobNames[job] || '알 수 없음';
};

const router = useRouter();

const redirectToPage = (id) => {
  router.push(`/item/detail/${id}`); // 이동할 페이지 경로를 여기에 설정
};
</script>

<style>
.card-2 {
  box-shadow: 0 3px 6px rgba(0,0,0,0.16), 0 3px 6px rgba(0,0,0,0.23); 
}

.card-2:hover {
  transform: scale(1.05); /* 카드가 위로 살짝 올라가는 효과 */
  box-shadow: 0 8px 16px rgba(0, 0, 0, 0.3);
  transition: transform 0.5s cubic-bezier(0.25, 0.8, 0.25, 1), 
              box-shadow 0.5s cubic-bezier(0.25, 0.8, 0.25, 1); /* 그림자 강화 */
}
</style>
