<script>
import NavBar from '@/components/NavBar.vue';
import SearchBox from '@/components/SearchBox.vue';

export default {
  name: 'ItemDetail',
  components: {
    NavBar,
    SearchBox,
  }
}
</script>

<script setup>
import { ref, onMounted, computed, watch } from 'vue';
import { useRoute } from 'vue-router';
import data from "@/assets/item/equip.json"

const items = data;

const selectedItem = ref(null);
const route = useRoute();

const fetchItem = () => {
  const id = route.params.id;
  selectedItem.value = items.find(item => item.id === Number(id));
};

onMounted(() => {
  fetchItem();
});

watch(() => route.params.id, fetchItem);

onMounted(() => {
  const id = route.params.id;
  selectedItem.value = items.find(item => item.id === Number(id));
});

const jobNames = [
  '직업제한무',
  '전사용',
  '도적용',
  '주술사용',
  '도사용',
];

const getJobName = (job) => {
  return jobNames[job] || '알 수 없음';
};

const isRepairable = computed(() => {
  return selectedItem.value.repair === 1 ? '가능' : '불가';
});

const isDrop = computed(() => {
  return selectedItem.value.drop === 1 ? '사망시 보존' : '사망시 떨어뜨림';
});

const isTransAction = computed(() => {
  return selectedItem.value.transAction === 1 ? '거래 가능' : '거래 불가';
});

const imageHeight = ref("100px");

const checkImageHeight = (event) => {
  const img = event.target; // 이미지 요소를 가져옴
  if (img.naturalHeight < 20) {
    imageHeight.value = "40px"; // 높이가 30px 미만일 때
  } else {
    imageHeight.value = "100px"; // 높이가 30px 이상일 때
  }
};
</script>

<style>
.bordered-card {
  border: 2px solid #000;
  padding: 15px 15px 0px 15px;
}
</style>
