<template>
  <v-container class="text-center">
    <h1>
      <span>바람의나라</span>
      <span class="ps-3">데이터베이스</span>
    </h1>

    <v-row class="mt-2" justify="center">
      <v-col cols="6">
        <v-menu>
          <template v-slot:activator="{ props }">
            <v-text-field 
              v-model="searchQuery" 
              v-bind="props" 
              label="아이템 검색" 
              hide-details 
              @input="isLoading = true" 
              @blur="isLoading = false"
            />
          </template>

          <v-list
            v-if="computedItemList.length > 0"
            class="border-e-thin border-b-thin border-s-thin rounded-b-lg blue-grey-lighten-4 item-list-container"
            style="box-shadow: 0 14px 28px rgba(0,0,0,0.25), 0 10px 10px rgba(0,0,0,0.22);"
          >
            <v-list-item
              v-for="item in computedItemList"
              :key="item.id"
              @click="goToItemDetail(item.id)"
            >
              <v-hover v-slot:default="{ isHovering, props }">
                <div
                  v-bind="props"
                  :style="{ 
                    backgroundColor: isHovering ? 'rgba(0, 0, 0, 0.1)' : 'transparent',
                    padding: '10px',
                    borderRadius: '4px' }"
                >
                  <div class="item-img-container me-4">
                    <v-img
                      class="item-img" 
                      :src="require(`@/assets/item/${item.id}.png`)"
                    />
                  </div>
                  <div class="mt-2 mb-2">
                    <v-text>{{ item.name }}</v-text>
                  </div>
                </div>
              </v-hover>
            </v-list-item>
          </v-list>
          
          <v-list
            v-else class="no-results-message border-e-thin border-b-thin border-s-thin rounded-b-lg blue-grey-lighten-4 item-list-container"
            style="box-shadow: 0 14px 28px rgba(0,0,0,0.25), 0 10px 10px rgba(0,0,0,0.22);"
          >
            <v-list-item>
              <div class="mt-2 mb-2">
                <div class="text-center" v-if="isLoading">
                  <v-progress-circular
                    color="grey-darken-1"
                    indeterminate
                  ></v-progress-circular>
                </div>
                <v-text v-else>검색할 아이템을 입력해 주세요.</v-text>
              </div>
            </v-list-item>
          </v-list>

         </v-menu>
      </v-col>
    </v-row>
  </v-container>
</template>

<style>
.item-list-container {
  max-height: 210px;
  overflow-y: auto;
}

.item-img-container {
  width: 35px;
  height: 35px;
  float: left;
  display: flex;
  justify-content: center;
  align-items: center;
}

.item-img {
  height: auto;
}
</style>

<script setup>
import { ref, computed } from 'vue';
import { useRouter } from 'vue-router';
import data from "@/assets/item/equip.json" // 추후에 Redis로 변경 예정

const router = useRouter();

const searchQuery = ref(null);
const isLoading = ref(false);

const items = data;

const computedItemList = computed(() => {
  if (!searchQuery.value) {
    return [];
  }
  return items
    .filter((item) => item.name.includes(searchQuery.value))
    .sort((a, b) => a.name.localeCompare(b.name));
});

const goToItemDetail = (id) => {
  const selectedItem = items.find(item => item.id === id);
  if (selectedItem) {
    router.push({
      name: 'ItemDetail',
      params: { id, item: JSON.stringify(selectedItem) }
    });
    searchQuery.value = null;
  }
};

</script>
